import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/index.vue'

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path: '/home',
      name: 'home',
      component: () => import('views/home'),
      children: [
        {
          path: 'setting',
          name: 'setting',
          alias: '/setting',
          component: () => import('views/User/setting'),
          children: [
            {
              path: 'profile',
              name: 'profile',
              alias: '/profile',
              component: () => import('views/User/Setting/profile'),
            },
            {
              path: 'notification',
              name: 'notification',
              alias: '/notification',
              component: () => import('views/User/Setting/notification'),
            },
          ],
        },
        {
          path: 'dashboard',
          name: 'dashboard',
          alias: '/dashboard',
          component: () => import('views/User/dashboard.vue'),
        },
      ],
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('views/login'),
    },
    {
      path: '/geeklogin',
      name: 'login',
      component: () => import('views/geeklogin'),
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('views/register.vue'),
    },
  ],

});



